import React from 'react';
import {Grid} from '@material-ui/core';
import {useEffect, useState} from 'react';
import MaterialTable from 'material-table';
import api from '../../utils/axios';
import {gridSpacing} from '../../store/constant';
// import toast, {Toaster} from 'react-hot-toast';
import {useHistory} from 'react-router-dom';
import CheckCircleIcon from '@material-ui/icons/CheckCircle';
import CancelIcon from '@material-ui/icons/Cancel';
import toast, {Toaster} from 'react-hot-toast';
import Cookies from 'js-cookie';

const ViewProducts = () => {
    const pageSize = 1000;
    const history = useHistory();
    const [isUpdate, setUpdate] = useState(false);
    const [products, setProducts] = useState([]);
    const [token] = useState(Cookies.get('strAccessToken'));
    const columns = [
        {field: 'id', title: 'id'},
        {field: 'strProductName', title: 'Product Name'},
        {field: 'strProductDescription', title: 'Product Description', width: '20%'},
        {field: 'strColour', title: 'Product Colour'},
        {field: 'strProductPrice', title: 'Product Price'},
        {field: 'strSize', title: 'Product Size'},
        {field: 'strShippingDays', title: 'Shipping Days'},
        {
            field: 'chrStatus',
            title: 'Status',
            lookup: {A: <CheckCircleIcon style={{color: '#91CC42'}} />, D: <CancelIcon style={{color: '#CC7242'}} />}
        },
        {field: 'datCreatedDate', title: 'Added Created Date'},
        {field: 'datModifiedDate', title: 'Modified Date date'}
        // {field: 'strCategory', title: 'Category '},
        // {field: 'strSubCategory', title: 'Sub Category'},
        // {field: 'blnWarrantyGuarantee', title: 'Warranty Guarantee'},
        // {field: 'blnTopSelling', title: 'Top Selling'},
        // {field: 'blnTax', title: 'Tax'},
        // {field: 'blnShippingCharges', title: 'Shipping Charges'},
        // {field: 'strMediaName', title: 'Product media name'},
        // {field: 'strMediaPath', title: 'Product media path'},
        // {field: 'chrType', title: 'Product Media type'}
    ];

    async function fetchGetData() {
        const response = await api.post('products/getProduct');
        setProducts(response.data);
        return response;
    }

    useEffect(() => {
        fetchGetData();
    }, [isUpdate]);
    function tConvert(time) {
        // Check correct time format and split into components
        time = time.toString().match(/^([01]\d|2[0-3])(:)([0-5]\d)(:[0-5]\d)?$/) || [time];
        if (time.length > 1) {
            // If time format correct
            time = time.slice(1); // Remove full string match value
            time[5] = +time[0] < 12 ? 'AM' : 'PM'; // Set AM/PM
            time[0] = +time[0] % 12 || 12; // Adjust hours
        }
        return time.join(''); // return adjusted time or original string
    }
    let arrData = products
        ? products.map((item) => {
              if (item.datCreatedDate !== []) {
                  if (item.datCreatedDate.indexOf('T') !== -1) {
                      let arrDateAndTime = item.datCreatedDate.split('T');
                      arrDateAndTime[1] = tConvert(arrDateAndTime[1].split('.')[0]);
                      arrDateAndTime = arrDateAndTime[0] + ' / ' + arrDateAndTime[1];
                      item.datCreatedDate = arrDateAndTime;
                  }
              }
              if (item.datModifiedDate !== null) {
                  if (item.datModifiedDate.indexOf('T') !== -1) {
                      let arrDateAndTime = item.datModifiedDate.split('T');
                      arrDateAndTime[1] = tConvert(arrDateAndTime[1].split('.')[0]);
                      arrDateAndTime = arrDateAndTime[0] + ' / ' + arrDateAndTime[1];
                      item.datModifiedDate = arrDateAndTime;
                  }
              }
              return item;
          })
        : products;
    return (
        <Grid container spacing={gridSpacing}>
            <Grid item xs={12}>
                <Grid container spacing={gridSpacing}>
                    <Grid item lg={12} md={12} sm={6} xs={12}>
                        <Toaster
                            toastOptions={{
                                className: '',
                                style: {
                                    borderRadius: '1rem',
                                    padding: '1rem',
                                    fontSize: '1rem',
                                    fontWeight: 600,
                                    color: '#ffff',
                                    backgroundColor: '#8BCC51'
                                }
                            }}
                        />
                        <MaterialTable
                            title="Products"
                            data={arrData}
                            columns={columns}
                            options={{
                                pageSizeOptions: [100, 250, 500, 1000],
                                pageSize: pageSize,
                                sorting: true,
                                exportButton: true,
                                actionsColumnIndex: -1,
                                addRowPosition: 'first',
                                loadingType: 'linear'
                            }}
                            actions={[
                                {
                                    icon: 'add_box',
                                    tooltip: 'Add New Product',
                                    position: 'toolbar',
                                    onClick: () => {
                                        let path = `/addProduct`;
                                        history.push(path);
                                    }
                                },
                                {
                                    icon: 'edit',
                                    tooltip: 'Edit Product',
                                    onClick: (event, rowData) => {
                                        let path = `/editProduct/${rowData.intProductPk}`;
                                        history.push(path);
                                    }
                                },
                                {
                                    icon: 'delete',
                                    tooltip: 'Delete Product',
                                    onClick: (event, rowData) =>
                                        new Promise((resolve, reject) => {
                                            let intDeletePk = rowData['intProductPk'];
                                            let deleteParams = {
                                                intProductPk: intDeletePk
                                            };
                                            if (window.confirm('Are you sure ?')) {
                                                async function fetchData() {
                                                    const response = await api.delete('products/deleteProduct', {
                                                        data: deleteParams,
                                                        headers: {
                                                            authorization: `Bearer ${token}`
                                                        }
                                                    });
                                                    return response;
                                                }
                                                fetchData();
                                                toast('Deleted successFully.', {
                                                    style: {
                                                        backgroundColor: '#D82D8A'
                                                    }
                                                });
                                            }
                                            async function fetchGetData() {
                                                const response = await api.post('products/getProduct');
                                                return response;
                                            }
                                            fetchGetData();
                                            setTimeout(() => {
                                                setUpdate(!isUpdate);
                                                resolve();
                                            }, 500);
                                        })
                                }
                            ]}
                        />
                    </Grid>
                </Grid>
            </Grid>
        </Grid>
    );
};

export default ViewProducts;
